@import url(https://fonts.googleapis.com/css?family=Nunito:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container{
  max-width: 2000px;
  overflow: hidden;
}

.scrollable {
  margin-right: -17px; /* オーバーレイスクロールバーを消す */
  padding-right: 17px; /* オーバーレイスクロールバーを消す */
  height: 300px;
  overflow: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.Primary{
  overflow: hidden !important;
  background-image: url(/takumi4913.github.io/static/media/sidebar_true2.4d6fa813.png);
}
.item {
  text-align: center;
  font-size: 3.5em;
  background-color: brown;
}
.NextItem {
  text-align: center;
  font-size: 3.5em;
  top: 0px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.NextItem_w {
  text-align: center;
  font-size: 3.5em;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
